body,
html {
  background: #fff;
  position: relative;
  overflow-x: hidden;
  font-family: "AA Stetica", sans-serif;
}

section {
  padding-bottom: 80px;
}

.container {
  max-width: 1452px;
  margin: 0 auto;
  padding: 0 15px;
  box-sizing: content-box;
}

a {
  color: #575352;
  transition: all 0.3s;
}
a:hover {
  color: #2b7818;
}

.btn__green {
  width: 100%;
  display: block;
  text-align: center;
  padding: 16px 20px;
  font: 500 20px "AA Stetica", sans-serif;
  color: #fff;
  transition: all 0.3s;
  border-radius: 80px;
  background: #5fb528;
  border: none;
}
.btn__green:hover {
  background: #2b7818;
}
.btn__trans {
  width: 100%;
  display: block;
  text-align: center;
  border: 1px solid #5fb528;
  border-radius: 80px;
  padding: 15px 19px;
  font: 500 20px "AA Stetica", sans-serif;
  color: #5fb528;
  transition: all 0.3s;
  border: none;
}
.btn__trans:hover {
  background: #f6fbe7;
}

.nav {
  width: 100%;
  display: grid;
  grid-template-columns: 82px 1fr;
  gap: 31px;
  padding: 50px 50px 0;
}
.nav__logo {
  display: block;
}
.nav__logo img {
  width: 100%;
}
.nav__row {
  background: #f7f7f7;
  border-radius: 100px;
  padding: 13px 50px 13px 33px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav__row ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 64px;
}
.nav__row ul a {
  font: 500 20px "AA Stetica", sans-serif;
  color: #575352;
}
.nav__phone {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  margin: 0 40px 0 auto;
}
.nav__phone p {
  font: 500 20px "AA Stetica", sans-serif;
  color: #2b7818;
  white-space: nowrap;
}
.nav__enter {
  font: 500 20px "AA Stetica", sans-serif;
  text-align: center;
  color: #575352;
}

.header {
  padding: 157px 0;
  position: relative;
  z-index: 2;
}
.header h1 {
  font: 900 44px/1.5 "AA Stetica", sans-serif;
  text-align: center;
  color: #32302f;
}
.header .btn__green {
  margin: 36px auto 0;
  max-width: 460px;
}
.header::before {
  content: "";
  width: 1605px;
  height: 429px;
  border-radius: 100%;
  background: rgba(11, 224, 21, 0.3);
  filter: blur(300px);
  position: absolute;
  top: 88px;
  right: -1242px;
  z-index: -1;
}
.header::after {
  content: "";
  display: block;
  width: 2773px;
  height: 429px;
  border-radius: 100%;
  background: rgba(178, 255, 63, 0.4);
  filter: blur(300px);
  position: absolute;
  top: 88px;
  left: -2415px;
  z-index: -1;
}

.about__tab {
  border-bottom: 1px solid #dedede;
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 115px;
}
.about__tab li {
  font: 700 36px "AA Stetica", sans-serif;
  color: #32302f;
  padding: 0 0 32px;
  transition: all 0.3s;
  position: relative;
}
.about__tab li::before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  bottom: -1px;
  position: absolute;
  left: 0;
  transition: all 0.3s;
  background: #5fb528;
  opacity: 0;
}
.about__tab li.active {
  color: #5fb528;
}
.about__tab li.active::before {
  opacity: 1;
}
.about__wrapper {
  margin: 40px 0 0;
  display: none;
}
.about__wrapper.active {
  display: block;
}
.about__center {
  display: grid;
  grid-template-columns: 1fr 436px;
  gap: 60px;
  align-items: center;
}
.about__center img {
  width: 100%;
  border-radius: 12px;
}
.about__center_text {
  display: grid;
  gap: 40px;
}
.about__center_text p {
  font: 400 24px/1.5 "AA Stetica", sans-serif;
  color: #2b7818;
}
.about__center_text h3 {
  font: 500 24px/1.5 "AA Stetica", sans-serif;
  color: #2b7818;
  margin: 11px 0 0;
}